import React from 'react';
import { withIntl } from '../i18n';
import PageWrapper from '../components/PageWrapper';
import Paragraphs from '../components/Paragraphs';
import { ContentTitle, PageTitle, Wysiwyg } from '../ui/utilities';

const Cookies = ({ t, ...context }) => {
  const cookiesSafeHarbour = t('cookies.safeHarbour');
  const cookiesConceptAnalytics = t('cookies.conceptAnalytics');
  const cookiesComplementAnalytics = t('cookies.complementAnalytics');
  const cookiesDeactivation = t('cookies.deactivation');

  const safeHarbour = `<a href="${cookiesSafeHarbour.link}" target="_blank" rel="noreferrer noopener">${cookiesSafeHarbour.title}</a>`;
  const conceptAnalytics = `<a href="${cookiesConceptAnalytics.link}" target="_blank" rel="noreferrer noopener">${cookiesConceptAnalytics.title}</a>`;
  const complementAnalytics = `<a href="${cookiesComplementAnalytics.link}" target="_blank" rel="noreferrer noopener">${cookiesComplementAnalytics.title}</a>`;
  const deactivation = `<p><a href="${cookiesDeactivation.link}" target="_blank" rel="noreferrer noopener">${cookiesDeactivation.title}</a></p>`;
  const sections = t('cookies.sections', {
    safeHarbour,
    conceptAnalytics,
    complementAnalytics,
    deactivation,
  });
  return (
    <PageWrapper title={t('cookies.title')} reader {...context}>
      <PageTitle>{t('cookies.title')}</PageTitle>
      <ContentTitle className="t-h1" aria-hidden="true">{t('cookies.title')}</ContentTitle>
      <Wysiwyg>
        <Paragraphs lines={t('cookies.description')} margin />
        {sections.map((section, index) => (
          <React.Fragment key={`cookies_section${index}`}>
            <h2>{section.title}</h2>
            <Paragraphs lines={section.description} margin />
          </React.Fragment>
        ))}
      </Wysiwyg>
    </PageWrapper>
  );
};

export default withIntl()(Cookies);
